'use client'; // Indispensable pour utiliser des boutons en Next.js

import { useState } from 'react';

export default function Home() {
  const [result, setResult] = useState<string>('En attente...');

  const testBackend = async () => {
    try {
      // On envoie le m√™me sc√©nario que tout √† l'heure (Coup interdit)
      const response = await fetch('http://localhost:8000/check-move', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          hand: [{ color: 'Rocket', value: 1 }, { color: 'Blue', value: 9 }],
          card_to_play: { color: 'Rocket', value: 1 },
          lead_card: { color: 'Blue', value: 5 }
        }),
      });

      const data = await response.json();
      
      // On affiche le r√©sultat
      if (data.valid === false) {
        setResult("‚úÖ Succ√®s : L'IA a bien d√©tect√© le coup interdit !");
      } else {
        setResult("‚ùå Bizarre : L'IA a accept√© le coup...");
      }

    } catch (error) {
      console.error(error);
      setResult("‚ùå Erreur : Impossible de parler au backend (V√©rifie que Docker tourne)");
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-900 text-white p-8">
      <h1 className="text-4xl font-bold mb-8 text-blue-400">The Crew AI üöÄ</h1>
      
      <div className="bg-gray-800 p-8 rounded-xl shadow-lg text-center max-w-md w-full">
        <p className="mb-6 text-gray-300">
          Clique ci-dessous pour v√©rifier si le site web arrive √† parler au serveur Python.
        </p>
        
        <button
          onClick={testBackend}
          className="bg-blue-600 hover:bg-blue-500 text-white font-bold py-3 px-6 rounded-lg transition-all transform active:scale-95"
        >
          Tester la connexion
        </button>

        <div className="mt-8 p-4 bg-gray-700 rounded border border-gray-600">
          <p className="font-mono text-sm">{result}</p>
        </div>
      </div>
    </div>
  );
}